            }
        },

        //函数防抖
        debounce(func, wait) {
            let timeout;
            return function () {
                let context = this;
                let args = arguments;
                if (timeout) clearTimeout(timeout);
                timeout = setTimeout(() => {
                    func.apply(context, args)
                }, wait);
            }
        },

        handleScroll(event){
            console.log("handleScrolling...")
            // event.preventDefault();
            this.direction = event.deltaY > 0 ? 'down' : 'up'
            // console.log(this.direction)
