                // console.log('scrollup')
                this.fontSize = this.fontSize - 0.5
                let options = {"fontSize": this.fontSize}
                this.monacoEditor.updateOptions(options)
            }else if(this.direction == "up"){
                // console.log('scrolldown')
                this.fontSize = this.fontSize + 0.5
                let options = {"fontSize": this.fontSize}
                this.monacoEditor.updateOptions(options)
            }
